@use 'sass:math' as math;

$primary: #3843ed;
$dark: #20272e;
$error: #ff776f;
$success: #71d475;

$primary-gradient: linear-gradient(180deg, #8892ff 0%, #6d6be5 18.75%);

/* ==== Mixin ==== */
$breakpoints: (
  'mobile': (
    max-width: 800px,
  ),
  'tablet': (
    min-width: 800px,
  ),
  'laptop': (
    min-width: 1000px,
  ),
  'desktop': (
    min-width: 1920px,
  ),
) !default;

/* ==== pxl to rem ==== */
@function rem($size) {
  @return math.div($size, 16px) * 1rem;
}

